import React from "react";
import "../sobreMim.css";

/**
 * Componente funcional que representa a pgina de sobre mim.
 * @return {JSX.Element} O componente JSX da pgina de sobre mim.
 */

function SobreMim() {
  return (
    <div className="SobreMim">
      <h1 className="centered-text welcomeText">Gabi - Maquiadora Profissional</h1>
      <p>DEIXA EU ME APRESENTAR... </p>

      <p>Gabriela Martins de Alcntara Silvano</p>
      <p>@gabrielasilvanno</p>
      <p>Sou maquiadora profissional, formada em 2020, 
        especialista em make beauty (maquiagem embelezadora).
      </p>
      <p>Nasci na cidade de Belo Horizonte, Minas Gerais, 
        cresci e moro em Ribeiro das Neves. Tenho 28 anos (10/10/1995).
        Sou Crist e no acredito em signos. Acredito somente no que a Palavra de Deus 
        fala sobre mim.
      </p>
      <p>No fiz faculdade, mas estudo muito a rea em que atuo. 
        Realizei alguns atendimentos a domiclio no incio da carreira. 
        Logo aps, montei meu estdio em casa, onde atendi principalmente 
        em maquiagem social, madrinhas e formandas at o ano de 2023.
      </p>
      <p>Em fevereiro de 2023, lancei meu curso online de automaquiagem 
        "Pele Sem Segredos", onde ensino desde o bsico at uma maquiagem super elaborada.
        Em 2024, pausei os atendimentos para focar no que estou amando: 
        criar contedo para o Instagram e TikTok, e tem dado muito certo!!!
      </p>
      <p>
        Estou animada com os novos rumos da minha carreira e comprometida em 
        compartilhar minha paixo pela maquiagem e beleza com o mundo. 
        Se voc tambm  apaixonado por esses assuntos, 
        acompanhe-me nas redes sociais para dicas, tutoriais e inspiraes. 
        Juntos, vamos explorar todo o potencial da maquiagem e da autoestima!
      </p>

    </div>
  );
}

export default SobreMim;
