// galeria.styles.ts
import styled from "styled-components";

export const Container = styled.div`
    background-color: #DCDCDC;
    border-radius: 10px;
    padding: 5px;
    position: relative; /* Adiciona posio relativa ao continer */

    img {
        max-width: 100%;
        display: block;
        margin-bottom: 10px;
        border-radius: 10px;
    }
`;

export const DeleteButton = styled.button`
    background-color: #FF0000; /* Vermelho */
    border: 0;
    color: #FFF;
    padding: 8px 16px;
    font-size: 15px;
    border-radius: 10px;
    cursor: pointer;
    transition: opacity 0.3s;

    position: absolute; /* Define a posio absoluta */
    bottom: 0; /* Alinha o boto na parte inferior */
    left: 50%; /* Alinha o boto no centro horizontal */
    transform: translateX(-50%); /* Centraliza horizontalmente */

    &:hover {
        opacity: 0.9;
    }
`;
