import { z } from 'zod';


export const schema = z.object({
    nome: z.string().min(5, "O nome  obrigatrio.").max(255, "O nome excedeu o tamanho mximo."),
    telefone: z.string().refine(value => {
        // Remover caracteres no numricos antes de validar
        const numericValue = value.replace(/\D/g, '');
        return numericValue.length >= 11 && numericValue.length <= 14;
    }, "Digite um nmero de telefone vlido."),
    dataNascimento: z.string().refine((value) => { 
        // Verifica se a data est entre 14 e 90 anos atrs da data atual
        const [day, month, year] = value.split('/');
        const parsedDate = new Date(`${year}-${month}-${day}`);
        const minDate = new Date();
        minDate.setFullYear(minDate.getFullYear() - 90); // 90 anos atrs
        const maxDate = new Date();
        maxDate.setFullYear(maxDate.getFullYear() - 14); // 14 anos atrs

        return parsedDate instanceof Date && parsedDate < maxDate && parsedDate > minDate;
    }, {
        message: 'Digite uma data vlida maior que 14 anos e menor que 90 anos no formato dd/mm/aaaa'
    }),
    tipoPele: z.string().min(1,"Selecione um tipo de pele."),
    tipoPagamento: z.string().min(1,"Selecione um tipo de pagamento."),
    modalidadeCurso: z.string().min(1,"Selecione a modalidade do curso."),
    temAlergia: z.string().min(1,"Selecione se tem alergia."),
    alergia: z.string().optional()
});

export const schemaNovoCurso = z.object({
    titulo: z.string().min(5, "O ttulo  obrigatrio!").max(255, "O ttulo excedeu o tamanho mximo."),
    descricao: z.string().min(5, "A descrio  obrigatria!").max(555, "A descrio excedeu o tamanho mximo."),
    cargaHoraria: z.string()
    .min(1, "Informe a carga horria do curso.")
    .refine(value => {
        const horas = parseInt(value);
        return horas > 0 && horas <= 30;
    }, {
        message: "A carga horria deve estar entre 1 e 30 horas."
    }),
    valor: z.string()
    .min(1, "Informe o valor do curso.")
    .refine(value => /^\d+(\.\d{1,2})?$/.test(value), {
        message: "Informe um valor vlido para o curso, com at duas casas decimais.ex: 10.00"
    }),
    dtInicialCurso: z.string().refine((value) => { 
        const [day, month, year] = value.split('/');
        const parsedDate = new Date(`${year}-${month}-${day}`);
        return !isNaN(parsedDate.getDate());
    }, {
        message: 'Digite uma data vlida no formato dd/mm/aaaa'
    }),
    dtFinalCurso: z.string().refine((value) => { 
        const [day, month, year] = value.split('/');
        const parsedDate = new Date(`${year}-${month}-${day}`);
        return !isNaN(parsedDate.getDate());
    }, {
        message: 'Digite uma data vlida no formato dd/mm/aaaa'
    }),
    ativo: z.coerce.boolean().optional(),
})

export const schemaCadastroUsuario = z.object({
    nomeCompleto: z.string()
        .min(3, "O nome completo deve ter no mnimo 3 letras.")
        .max(100, "O nome completo no pode exceder 100 caracteres."),
    usuario: z.string()
        .min(4, "O usurio deve ter no mnimo 4 caracteres.")
        .max(16, "O usurio no pode exceder 16 caracteres.")
        .regex(/^\S*$/, "O usurio no pode conter espaos."),
    senha: z.string()
        .min(4, "A senha deve ter no mnimo 4 caracteres.")
        .max(16, "A senha no pode exceder 16 caracteres.")
});

